import React from "react";
import { Form, reduxForm} from "redux-form";
import { FieldCreatorFunc, FormForInput} from "../common/FormCreate/FormInput";
import {maxLenghtValues, required} from "../common/validateForm/validateForm";
import {connect} from "react-redux";
import {loginThunkCreator} from "../../redux/headerReduser";
import {Navigate} from "react-router-dom";
import s from "../common/validateForm/validare.module.css"

const maxLenghLogin = maxLenghtValues(50)
const LoginForm = (props) => {
    return (
        <Form onSubmit={props.handleSubmit}>
            {FieldCreatorFunc([required, maxLenghLogin], FormForInput, "Login", 'login' )}
            {FieldCreatorFunc([required, maxLenghLogin], FormForInput, "Password", 'password' )}
            {FieldCreatorFunc(null,`input`, null,"rememberme", 'Remember me', {type: "checkbox"} )}

            {props.error && <div className={s.Error}>
                {props.error}
            </div>}

            <div>
                <button>Login</button>
            </div>
        </Form>

    )
}


const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm)


const Login = (props) => {

    let onSubmit = (formData) => {
        props.loginThunkCreator(formData.login, formData.password, formData.rememberme)
    }

    if (props.isAuth) {
        return (
            <Navigate to={'/profile'}/>
        )
    }
    return (
        <div>
            <h1>Please authorized</h1>
            <LoginReduxForm onSubmit={onSubmit}/>
        </div>
    )
}

let mapStateToProps = (state) => {
    return {
        isAuth: state.headerPage.isAuth
    }
}
export default connect(mapStateToProps, {loginThunkCreator})(Login)