import {headerThunk} from "./headerReduser";

let initializedValue = "INITIALIZED-VALUE"

let initialState = {
    initializedSuccess: false,
}
const appReduser = (state=initialState, action)=>{
    switch (action.type){
        case initializedValue:
        return {
            ...state, initializedSuccess: true,
        }
        default:
            return state
    }
}

const initializedAC = ()=>({type:initializedValue })


export const initializedTH = ()=>(dispatch)=>{
   let promise= dispatch(headerThunk())
    Promise.all([promise]).then(()=> {
            dispatch(initializedAC())
        }
    )


}


export default appReduser