import React from 'react';
import s from './Dialogs.module.css'
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import {Field, Form, reduxForm} from "redux-form";
import {FieldCreatorFunc, FormForInput, FormForTextArea} from "../common/FormCreate/FormInput";
import {maxLenghtValues, required} from "../common/validateForm/validateForm";



const Dialogs = (props) => {
    let dialogsElements = props.dialogsPage.dialogsData.map(dialog => <DialogItem name={dialog.name} id={dialog.id}/>)
    let messagesElements = props.dialogsPage.messageData.map(message => <Message message={message.message} id={message.id}/>)
    let addMessage = (values) => {
        props.addMessageContainer(values.newPostText)
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogs_items}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                {messagesElements}
                <div className={s.addMessage}>
                   <DialogsPostFormRedux onSubmit={addMessage}/>
                </div>
            </div>

        </div>
    )
}
const  maxLenghtValuesDialogs = maxLenghtValues(15)
const DialogsPostForm = (props)=>{
    return (
        <Form onSubmit={props.handleSubmit}>
            {FieldCreatorFunc ([required, maxLenghtValuesDialogs], FormForTextArea, "Введите сообщение","newPostText" , null, {cols: "10", rows: "5" }  )}
            <button>Add Post</button>
        </Form>
    )
}
const DialogsPostFormRedux = reduxForm({
    form: "dialogsPostForm"
})(DialogsPostForm)
export default Dialogs