import React, {Suspense} from 'react';
import './App.css';
import Nav from "./componrts/Nav/Nav";
import {Routes, Route} from "react-router-dom";
import News from "./componrts/News/News";
import Music from "./componrts/Music/Music";
import Settings from "./componrts/Settings/Settings";
import DialogsContainer from "./componrts/Dialogs/DialogsContainer";
import  {withRouter} from "./componrts/Profile/ProfileContainer";
import HeaderContainer from "./componrts/Header/HeaderContainer";
import Login from "./componrts/Login/Login";
import {connect} from "react-redux";
import {compose} from "redux";
import {initializedTH} from "./redux/appReduser";
import Preloader from "./componrts/common/preloader/preloader";
const UsersContainer = React.lazy(() => import('./componrts/Users/UsersContainer'));
const ProfileContainer = React.lazy(() => import('./componrts/Profile/ProfileContainer'));

class App extends React.Component {
    componentDidMount() {
        this.props.initializedTH()
    }

    render() {
        if (!this.props.initializedSuccess) {
            return (<Preloader/>)
        }
        return (
            <div className='app-wrapper'>
                <HeaderContainer/>
                <Nav/>
                <div className="app-wrapper-content">
                    <Suspense fallback={<div><Preloader /></div>}>
                    <Routes>
                        <Route path="/Profile/:id?" element={<ProfileContainer/>}/>
                        <Route path="/Massage/*" element={<DialogsContainer store={this.props.store}/>}/>
                        <Route path="News" element={<News/>}/>
                        <Route path="Music" element={<Music/>}/>
                        <Route path="Settings" element={<Settings/>}/>
                        <Route path="Users" element={<UsersContainer/>}/>
                        <Route path="/login/*" element={<Login/>}/>
                    </Routes>
                        </Suspense>
                </div>
            </div>
        );
    }
}

let mapStateToProps = (state) => ({
    initializedSuccess: state.appReduser.initializedSuccess
})

export default compose(
    connect(mapStateToProps, {initializedTH}),
    withRouter,)(App)
