import userDAL from "../api/userApi";
import {updateObjectInArray} from "../componrts/common/objectHelpers/objectHelpers";

let follow = "FOLLOW"
let unfollow = "UNFOLLOW"
let setUsers = "SET_USERS"
let usersCount = 'USERS-COUNT'
let usersActive = "USER-ACTIVE"
let isFeatching = 'ISFEATCHING'
let isLoading = 'ISLOADING'


let initialState = {
    usersPage: [],
    usersCount: 10,
    usersAll: 1,
    usersActive: 1,
    isFeatching: false,
    UsersId: [],
    portionUsers: 10
}

const UsersReduser = (state = initialState, action) => {

    switch (action.type) {
        case follow:
            return {
                ...state,
                usersPage: updateObjectInArray(state.usersPage, "id", action.userId, {followed: true})
            }
        case unfollow:
            return {
                ...state,
                usersPage: updateObjectInArray(state.usersPage, "id", action.userId, {followed: false})
            }
        case setUsers:

            return {
                ...state,
                usersPage: [...action.user]
            }
        case usersCount:
            return {...state, usersAll: action.count}


        case usersActive:
            return {
                ...state, usersActive: action.active
            }
        case isFeatching:
            return {
                ...state, isFeatching: action.featching
            }
        case isLoading:
            return {
                ...state,
                UsersId: action.loading
                    ? [...state.UsersId, action.id]
                    : state.UsersId.filter(id => id != action.id)
            }


        default:
            return state;
    }
}

export const followAC = (userId) => ({type: follow, userId})
export const unfollowAC = (userId) => ({type: unfollow, userId})
export const setUsersAC = (user) => ({type: setUsers, user})
export const setCountAC = (count) => ({type: usersCount, count})
export const setActiveAC = (active) => ({type: usersActive, active})
export const setUserFeatchingAC = (featching) => ({type: isFeatching, featching})
export const setUserLoadingAC = (loading, id) => ({type: isLoading, loading, id})


export const usersThunk = (usersActive, usersCount) => {
    return (dispatch) => {
        dispatch(setUserFeatchingAC(true));
        userDAL.userApi(usersActive, usersCount).then(data => {
            dispatch(setActiveAC(usersActive))
            dispatch(setUsersAC(data.items));
            dispatch(setCountAC(data.totalCount));
            dispatch(setUserFeatchingAC(false));
        })
    }
}

const followUnfollow = async (dispatch, userID,apiMethod,actionCreate )=>{
    dispatch(setUserLoadingAC(true, userID));
    let data = await apiMethod(userID);
    if (data.resultCode === 0) {
        dispatch(actionCreate(userID))
    }
    dispatch(setUserLoadingAC(false, userID))
}

export const followThunk = (userID) => {
    return async (dispatch) => {
        followUnfollow(dispatch, userID, userDAL.userFollow.bind(userDAL), unfollowAC)
    }
}

export const unfollowThunk = (userID) => {
    return async (dispatch) => {
        await followUnfollow(dispatch, userID, userDAL.userUnfollow.bind(userDAL), followAC)
    }
}

export default UsersReduser