import React from 'react';
import s from './Post.module.css'

const Post = (props) => {
    return (
        <div className={s.item}>
            <img src="https://i.pinimg.com/736x/2b/f2/4f/2bf24f610dff38e1ba5034efcebb6c5f.jpg" alt=""/>
           {props.massage}
            <div>
            <span>Like</span> {props.countLike}
            </div>
        </div>

    )
}

export default Post