import React from 'react';
import s from "./Nav.module.css";
import {NavLink} from "react-router-dom";

const Nav = () => {
    return (
        <nav className={s.nav}>
            <div>
                <NavLink className={s.item} to="/Profile" className={({isActive}) => (isActive ? s.activeLink:undefined)}>Profile</NavLink>
            </div>
            <div>
                <NavLink  className={s.item} to="/Massage" className={({isActive}) => (isActive ? s.activeLink:undefined)}>Massage</NavLink >
            </div>
            <div>
                <NavLink  className={s.item} to="/News" className={({isActive}) => (isActive ? s.activeLink:undefined)}>News</NavLink >
            </div>
            <div>
                <NavLink  className={s.item} to="/Music" className={({isActive}) => (isActive ? s.activeLink:undefined)}>Music</NavLink >
            </div>
            <div>
                <NavLink  className={s.item} to="/Users" className={({isActive}) => (isActive ? s.activeLink:undefined)}>Users</NavLink >
            </div>
            <div>
                <NavLink  className={s.item} to="/Settings" className={({isActive}) => (isActive ? s.activeLink:undefined)}>Settings</NavLink >
            </div>
        </nav>

    )
}

export default Nav;

