import message from "../componrts/Dialogs/Message/Message";

let newMessageBody = "NEW-TEXT-MESSAGE";
let addNewMessage = "ADD-NEW-MESSAGE";


let inilialState= {
    dialogsData: [{name: 'Sergey', id: 1},
        {name: 'Sanches', id: 2},
        {name: 'Sveta', id: 3},
        {name: 'Bob', id: 4},
        {name: 'Jon', id: 5},
        {name: 'Dima', id: 6},],

    messageData: [{message: 'Hi', id: 1,},
        {message: 'How is your it-kamasutra?', id: 2,},
        {message: 'Yo', id: 3,}
    ],
    newMessageText: "",

};
const DialogsPageReduser = (state=inilialState, action) => {
    switch (action.type) {
        case addNewMessage:{
            return {
            ...state, messageData: [...state.messageData, {message: action.textMessage, id: 0}]
            }
        }
        default:
            return state
    }
}
export const addNewMessageActionCreate = (textMessage)=>{
    return {type: addNewMessage, textMessage}
}

export default DialogsPageReduser
