import React, {useEffect, useState} from "react";


const StatusComponentWithHooks = (props) => {

    const lokalStateStatus = useState(false)
    const [status, editModeStatus] = lokalStateStatus

    const [lokalInput, editModeInput]  = useState(props.status)

    useEffect(()=> {
        editModeInput(props.status)
    }, [props.status])

    const valueActiv = ()=>{
        editModeStatus(true)
    }

    const valueDeActiv = ()=>{
        editModeStatus(false)
        props.statusSaveThunkCreator(lokalInput)
    }

    const onChangeStatus = (e)=>{
        editModeInput(e.target.value)
    }
    return (
        <div>
            {(!status ? <span onDoubleClick={valueActiv}>{(props.status || "----")}</span> :
                <div> <input value={lokalInput} onChange={onChangeStatus} onBlur={valueDeActiv} autoFocus={true}/> </div>)}
        </div>
    )
}


export default StatusComponentWithHooks