import React from 'react';
import s from './MyPosts.module.css'
import Post from "./Post/Post";
import {Field, Form, reduxForm} from "redux-form";
import {FormForInput, FormForTextArea} from "../../common/FormCreate/FormInput";
import {maxLenghtValues, required} from "../../common/validateForm/validateForm";

const MyPosts = React.memo(props=>  {
    //  shouldComponentUpdate(nextProps, nextState){
    //    return this.props !== nextProps || this.state !== nextState
    // }
    let postsElemet = props.postData.map(post => <Post massage={post.message} countLike={post.countLike}/>)

    let addPost = (values) => {
       props.addPostContainer(values.newMessage)
    }
        return (
            <div className={s.postsBlock}>
                <h3>My posts</h3>
                <div>
                    <MessageFormRedux onSubmit={addPost}/>
                </div>
                <div className={s.posts}>
                    {postsElemet}
                </div>
            </div>
        )
    })

const maxLenghtValuesMyPosts = maxLenghtValues (20)
const MessageForm = (props)=>{
    return (
        <Form onSubmit={props.handleSubmit}>
            <div>
                <Field type="textarea" validate={[required, maxLenghtValuesMyPosts]} component={FormForTextArea} name="newMessage" id="" cols="30" rows="10"></Field>
            </div>
            <div>
                <button>Add post</button>
            </div>
        </Form>
    )
}
const MessageFormRedux = reduxForm({
    form: 'NewTextMessageForm'
})(MessageForm)

export default MyPosts