import React from 'react';
import s from './ProfileInfo.module.css'
import Preloader from "../../common/preloader/preloader";
import StatusComponentWithHooks from "./StatusComponentWithHooks";

const ProfileInfo = (props) => {

    if (props.profile===null ) {
        return <Preloader/>
    }
    return <div>
        <div>
            <img
                src="https://adonius.club/uploads/posts/2022-05/thumbs/1653266572_11-adonius-club-p-oboi-na-rabochii-stol-tokio-krasivo-13.jpg"
                alt=""/>
        </div>
        <div className={s.descriptionBlock}>
            <img src={props.profile.photos.large}/>
        </div>
        <div>
            <StatusComponentWithHooks profile={props.UsersData} status={props.status} statusSaveThunkCreator={props.statusSaveThunkCreator}/>
        </div>
    </div>
}


export default ProfileInfo