import userDAL, {profileDAL} from "../api/userApi";

let addPosttype = "ADD-POSTS";
let UsersData = "USERS-DATA";
let StatusData = "STATUS-DATA";
let DeletePost = "DELETE-POST"
let initalState = {

    postData: [
        {message: "Hi, how are you?", countLike: 15, id: 1,},
        {message: "It's my first post", countLike: 30, id: 2,},
    ],
    textNew: "",
    UsersData: null,
    status: ""
}
const profilePageReducer = (state = initalState, action) => {
    switch (action.type) {
        case addPosttype:
            return {
                ...state, postData: [...state.postData, {message: action.message, countLike: 0, id: 0} ],
            }
        case UsersData:
            return {
                ...state, UsersData: action.info
            }
        case StatusData:
            return {
                ...state, status: action.userStatus
            }
        case DeletePost:
            return {
                ...state, postData: state.postData.filter(u=>u.id !== action.idPost)
            }
        default:
            return state
    }
}
export const addPostActionCreate = (message) => ({type: addPosttype, message})
export const UsersDataAC = (info) => ({type: UsersData, info})
export const UsersStatusAC = (userStatus) => ({type: StatusData, userStatus})
export const deletePostActionCreate = (idPost) =>({type: DeletePost, idPost })

export const profileThunkCreator = (userId)=> (dispatch) =>{
        userDAL.profileApi(userId).then(response => {
            dispatch(UsersDataAC(response.data))
        })
}
export const statusThunkCreator = (userId)=> async (dispatch) =>{
    let response = await profileDAL.statusApi(userId);
        dispatch (UsersStatusAC(response.data))
}
export const statusSaveThunkCreator = (newStatus)=> async (dispatch) =>{
    let response = await  profileDAL.saveStatusApi(newStatus);
        if (response.data.resultCode === 0) {
            dispatch(UsersStatusAC(newStatus))
        }
}
export default profilePageReducer
