import React from "react";
import {connect} from "react-redux";
import Profile from "./Profile";
import {
    profileThunkCreator,
    statusSaveThunkCreator,
    statusThunkCreator,
    UsersDataAC,
    UsersStatusAC
} from "../../redux/profileReducer";
import {useParams} from "react-router-dom";
import { compose } from 'redux';
import withAuth from "../Hoc/WithAuth";


export function withRouter(Children) {
    return (props) => {
        const match = {params: useParams()};
        return <Children {...props} match={match}/>
    }
}


class ProfileContainer extends React.Component{
    componentDidMount() {
        let userId=this.props.match.params.id
        if (userId ==null || userId ==undefined){
            userId=this.props.userId;
        }
        this.props.profileThunkCreator(userId)
        this.props.statusThunkCreator(userId)
    }

    render() {
        return  <Profile {...this.props} />
    }
}

let mapStateToProps = (state)=>({
    UsersData: state.profilePage.UsersData,
    status: state.profilePage.status,
    userId: state.headerPage.id
   })

export default compose(
    withRouter,
    connect(mapStateToProps, {statusSaveThunkCreator, statusThunkCreator, UsersDataAC, profileThunkCreator, UsersStatusAC}),
    withAuth,
    )(ProfileContainer)