import React from "react";
import s from "../validateForm/validare.module.css"
import {Field} from "redux-form";



const FormControl = ({ input, meta: {touched, error}, children, ...props})=>{
    const metaData =  touched && error
    return<div>
        <div className={( metaData && s.formControl)}>
            {children}
        </div>
        <div>
            {(metaData && <span className={s.Error}> {error}</span>)}
        </div>
    </div>
}
export const FormForInput = (props)=>{

    const { input, meta, child, ...restprops} = props
    return (
        <FormControl {...props}>
            <input {...input} {...restprops}/>
        </FormControl>
    )
}

export const FormForTextArea = (props)=>{
    const { input, meta, ...restprops} = props
    return (
        <FormControl {...props}>
            <textarea {...input} {...restprops}/>
        </FormControl>
    )
}
export const FieldCreatorFunc = (validators, component, placeholder, name, text, props={})=> {
    return (

        <div>
            <Field validate={validators} component={component} placeholder={placeholder}
                   name={name} {...props}/>{text}
        </div>
    )
}