import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import profilePageReducer from "./profileReducer";
import DialogsPageReduser from "./dialogsReducer";
import UsersReduser from "./usersReducer";
import headerPageReducer from "./headerReduser";
import thunkMiddleware from "redux-thunk";
import {reducer as formReduser} from "redux-form"
import appReduser from "./appReduser";

let redusers= combineReducers({
    profilePage: profilePageReducer,
    dialogsPage: DialogsPageReduser,
    usersPage: UsersReduser,
    headerPage: headerPageReducer,
    appReduser: appReduser,
    form: formReduser,
})

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(redusers, composeEnhancers(   applyMiddleware(thunkMiddleware)))
export default store