import axios from "axios";

let instance = axios.create({
    baseURL: `https://social-network.samuraijs.com/api/1.0/`,
    withCredentials: true,
    headers: {
        "API-KEY": '2541bf33-d69b-4281-8aea-1ce3d670fcda'
    },
})


 const userDAL = {
     userApi (usersActive,usersCount) {
         return instance.get (`users?page=${usersActive}&count=${usersCount}`).then(response =>{
             return response.data})
     },

     headerApiGet(){
         return instance.get ('auth/me').then(response =>{return response.data})
     },

     userFollow (userID){
         return  instance.delete(`follow/` + userID).then(response => {return response.data})
     },
     userUnfollow (userID){
         return  instance.post(`follow/` + userID).then(response => {return response.data})
     },

     profileApi (userId){
         return profileDAL.profileApi(userId)
     },

}
export  const profileDAL ={
    profileApi (userId){
        return instance.get (`profile/` + userId)
    },

    statusApi (userId){
        return instance.get (`/profile/status/` +userId)
    },
    saveStatusApi (newStatus){
        return instance.put (`/profile/status/`, {status:newStatus})
    }
}

export const authDAL = {
    login(email,password, rememberMe ){
        return instance.post ('auth/login', {email, password, rememberMe})
    },
    logOut(){
        return instance.delete ('auth/login')
    }
}

export  default userDAL