import userDAL, {authDAL} from "../api/userApi";
import {stopSubmit} from "redux-form";

let headerAuthMe = "HEADER-AUTH-ME";


let initalState = {

    id: null,
    login: null,
    email: null,
    rememberMe: null,
    isAuth: false,
    password: null,

}
const headerPageReducer = (state = initalState, action) => {
    switch (action.type) {
        case headerAuthMe:
            return {
                ...state, ...action.data,
            }
        default:
            return state
    }
}

export const headerAuthMeAC = (id, email, login, isAuth) => ({type: headerAuthMe, data: {id,  email, login, isAuth}})
export const  headerThunk = ()=> async (dispatch)=>{
         let data = await userDAL.headerApiGet();
            if (data.resultCode ===0){
                let {id, login, email}=data.data
                dispatch(headerAuthMeAC(id,  email, login,true))
            }
    }


export const loginThunkCreator = (email, password, rememberMe )=> async (dispatch) =>{
   let response = await authDAL.login(email, password, rememberMe );
        if (response.data.resultCode ===0){
            dispatch(headerThunk())
        }else {
            dispatch(stopSubmit('login', {_error: (response.data.messages.length === 1 ? response.data.messages[0] : 'some Error')} ))
        }
}
export const logOutThunkCreator = ()=> async (dispatch) =>{
    let response = await authDAL.logOut();
        if (response.data.resultCode ===0){
            dispatch(headerAuthMeAC(null, null, null, false),)
            dispatch(headerThunk())
        }
}
export default headerPageReducer
