import React from 'react';
import {connect} from "react-redux";
import Header from "./Header";
import {headerAuthMeAC, headerThunk, logOutThunkCreator} from "../../redux/headerReduser";




class HeaderContainer extends React.Component{
    render() {
        return (
            <Header headerPage={this.props.headerPage} logOutThunkCreator={this.props.logOutThunkCreator}/>
        )
    }
}
let mapStateToProps = (state)=>({
    headerPage: state.headerPage,
})

export default connect (mapStateToProps, {headerAuthMeAC , headerThunk, logOutThunkCreator})(HeaderContainer)