import React from 'react';
import s from './Header.module.css'
import {NavLink} from "react-router-dom";
const Header = (props) => {
    return (
        <header className={s.Header}>
            <img
                src='https://cdn.shopify.com/s/files/1/0379/2289/8060/products/Fanta-Logo_1200x1200.png?v=1587470010'/>

            {props.headerPage.isAuth ? <div className={s.login}>{props.headerPage.login}  <button onClick={props.logOutThunkCreator}>LogOut</button> </div>
                :<NavLink  className={s.login} to={'/login'}> Login</NavLink> }


        </header>

    )
}
export default Header;